import React, { Component } from 'react';
import FondoWeb from "./assets/images/bg/espacio.png";
import SafeImage from "./assets/images/keys/CajaFuerte.png"
import MetamaskIco from "./assets/images/metamask.svg"
import * as s from "./styles/globalStyles";
import KeyRenderer from "./components/KeyRenderer";
import Web3 from 'web3'
import KeyNFT from './abis/KeysNFT.json'

class App extends Component {
  async componentWillMount(){
    await this.loadWeb3()
    await this.loadBlockchainData()
  }

  async loadWeb3() {
    if(window.web3) {
      window.web3 = new Web3(window.web3.currentProvider)
    }
  }

  async ConectaMetamask(){
    if(window.ethereum) {
      try{
        window.web3 = new Web3 (window.ethereum)
        await window.ethereum.enable()
        this.setState({ButtonConectar: "hidden"})
        this.loadBlockchainData()
      }catch{
      }
    }
    else if (window.web3) {
      window.web3 = new Web3 (window.web3.currentProvider)
    }
    else{
      window.alert('No se ha encontrado Metamask!')
    }
  }

  async loadBlockchainData(){
    const web3 = window.web3
    // Cargar una cuenta
    const accounts = await web3.eth.getAccounts()
    // eslint-disable-next-line
    const networkId = await web3.eth.net.getId() // Rinkeby -> 4 , Ganache -> 5777
    // eslint-disable-next-line
    if (networkId != 97 && networkId != 5777){
      window.alert('Tienes que estar en la TestNet de Binance')
      window.open("https://academy.binance.com/es/articles/connecting-metamask-to-binance-smart-chain", "_blank")
    }
    // eslint-disable-next-line
    if (accounts.length != 0){
      this.setState({account: accounts[0]})
      const balance_account = await Web3.utils.fromWei(await web3.eth.getBalance(this.state.account),'ether')
      this.setState({balance_account: parseFloat(balance_account).toFixed(4) + ' BNB'})
    }else{
      // No hay cuenta Metamask Logueada, Mostramos el botón de Conectar
      // eslint-disable-next-line
      this.setState({ButtonConectar: "visible"})
    }

    const networkData = KeyNFT.networks[networkId]
    if(networkData) {
      const abi = KeyNFT.abi
      const address = networkData.address
      this.setState({contract_address: address})
      try{ // Get Objeto Contrato
        const contract = new web3.eth.Contract(abi, address)
        this.setState({contract: contract})
      }catch(err){
        console.log("GetContractObject:",err.message)
      }
      try{ // Get Precio de 1 Llave
        const Precio_Llave = await this.state.contract.methods.fee().call()
        this.setState({Precio_Llave: Web3.utils.fromWei(Precio_Llave, 'ether')})
      }catch(err){
        console.log("GetPrecioLlave:",err.message)
      }
      try{ // Get Owner
        const Owner = await this.state.contract.methods.owner().call()
        this.setState({owner: Owner})
      }catch(err){
        console.log("GetOwner:",err.message)
      }
      try{ // Get Balance Total Caja
        const Balance_Caja = await this.state.contract.methods.moneySmartContract().call()
        this.setState({Balance_Caja: Web3.utils.fromWei(Balance_Caja, 'ether')})
      }catch(err){
        console.log("GetBalanceCaja:",err.message)
      }
      try{ // Get Porcentaje Premio
        const ProcentPremio = await this.state.contract.methods.ProcentPremio().call()
        this.setState({ProcentPremio: ProcentPremio})
      }catch(err){
        console.log("GetPorcentajeCaja:",err.message)
      }
      try{ // Get Premio Caja
        const Premio_Caja = await this.state.contract.methods.GetPremio().call()
        this.setState({Premio_Caja: Web3.utils.fromWei(Premio_Caja, 'ether')})
      }catch(err){
        console.log("GetPremioCaja:",err.message)
      }
      try{ // Config Llave de Hierro
        const Configs1 = await this.state.contract.methods.Configs(1).call()
        this.setState({MintIron: Configs1.mint})
        this.setState({JuegoIron: Configs1.juego})
      }catch(err){
        console.log("GetConfigLlaveHierro:",err.message)
      }
      try{ // Config Llave de Plata
        const Configs2 = await this.state.contract.methods.Configs(2).call()
        this.setState({MintSilver: Configs2.mint})
        this.setState({JuegoSilver: Configs2.juego})
      }catch(err){
        console.log("GetConfigLlavePlata:",err.message)
      }
      try{ // Config Llave de Oro
        const Configs3 = await this.state.contract.methods.Configs(3).call()
        this.setState({MintGold: Configs3.mint})
        this.setState({JuegoGold: Configs3.juego})
      }catch(err){
        console.log("GetConfigLlaveOro:",err.message)
      }
      // eslint-disable-next-line
      if (accounts.length != 0){
        try{ // Carga la lista de Mis Llaves
          const MisLlaves = await this.state.contract.methods.getOwnerKeys().call({from: this.state.account})
          this.setState({MisLlaves: MisLlaves})
        }catch(err){
          console.log("GetMisLlaves:",err.message)
        }
        // Comprueba que sea el Owner para mostar panel Admin
        // eslint-disable-next-line
        if (this.state.owner == this.state.account){
          this.setState({AdminPanel: "visible"})
        }else{
          this.setState({AdminPanel: "hidden"})
        }
      }  
    }

  }

  // Constructor
  constructor(props) {
    super(props)
    this.state = {
      account: '',
      balance_account: '',
      owner: '',
      contract_address: null,
      contract: null,
      loading: false,
      errorMessage: '',
      ButtonConectar: "hidden",
      AdminPanel: "hidden",
      Balance_Caja: 0,
      Premio_Caja: 0,
      Precio_Llave: 0,
      MisLlaves: [],
      ProcentPremio: 0,
      MintIron: '',
      MintSilver: '',
      MintGold: '',
      JuegoIron: '',
      JuegoSilver: '',
      JuegoGold: '',
    }
    this.Admin =''
  }

  Mint_Llave = async (mensaje,cant) => {
    try {
      await this.state.contract.methods.createRandomKeyMulti().send({from: this.state.account, value: Web3.utils.toWei(String(this.state.Precio_Llave * cant), 'ether') })
    }catch(err){
      this.setState({errorMessage: err.message})
      console.log("error:",err.message)
    } finally {
      this.setState({loading: false})
      this.loadBlockchainData()
    }
  }

  Usa_Llave = async (KeyID,mensaje) => {
    let Resp = 0
    try {
      Resp = await this.state.contract.methods.Juega(KeyID).send({from: this.state.account})
    }catch(err){
      this.setState({errorMessage: err.message})
      console.log("error:",err.message)
    } finally {
      if (Resp.events.NewWin){
        window.alert('Has Ganado')
      }
      this.setState({loading: false})
      this.loadBlockchainData()
    }
  }

  // Parte ADMIN
  AdminPrecioLlave = async (mensaje) => {
    let Input = window.prompt(mensaje);
    try {
      await this.state.contract.methods.updateFee(Web3.utils.toWei(String(Input), 'ether')).send({from: this.state.account})
    }catch(err){
      this.setState({errorMessage: err.message})
      console.log("error:",err.message)
    } finally {
      this.setState({loading: false})
      this.loadBlockchainData()
    }
  }
  AdminPorcentPremio = async (mensaje) => {
    let Input = window.prompt(mensaje);
    try {
      await this.state.contract.methods.SetProcentPremio(Input).send({from: this.state.account})
    }catch(err){
      this.setState({errorMessage: err.message})
      console.log("error:",err.message)
    } finally {
      this.setState({loading: false})
      this.loadBlockchainData()
    }
  }

  AdminConfigLlaves = async (mensaje) => {
    let MintIron = window.prompt("% Minteo Iron",this.state.MintIron);
    let JuegoIron = window.prompt("% Caja Iron",this.state.JuegoIron);
    let MintSilver = window.prompt("% Minteo Silver",this.state.MintSilver);
    let JuegoSilver = window.prompt("% Caja Silver",this.state.JuegoSilver);
    let MintGold = window.prompt("% Minteo Golden",this.state.MintGold);
    let JuegoGold = window.prompt("% Caja Golden",this.state.JuegoGold);
    // [[50,10],[35,30],[15,50]]
    let Array = [[MintIron,JuegoIron],[MintSilver,JuegoSilver],[MintGold,JuegoGold]]
    try {
      await this.state.contract.methods.SetConfigs(Array).send({from: this.state.account})
    }catch(err){
      this.setState({errorMessage: err.message})
      console.log("error:",err.message)
    } finally {
      this.setState({loading: false})
      this.loadBlockchainData()
    }
  }
  AdminRugPull = async (mensaje) => {
    try {
      await this.state.contract.methods.withdraw().send({from: this.state.account})
    }catch(err){
      this.setState({errorMessage: err.message})
      console.log("error:",err.message)
    } finally {
      this.setState({loading: false})
      this.loadBlockchainData()
    }
  }
  RegalaLlave = async (mensaje) => {
    let Input = window.prompt(mensaje);
    try {
      await this.state.contract.methods.MintRandomKey(Input).send({from: this.state.account})
    }catch(err){
      this.setState({errorMessage: err.message})
      console.log("error:",err.message)
    } finally {
      this.setState({loading: false})
      this.loadBlockchainData()
    }
  }
  AdminCargaCaja = async (mensaje) => {
    let Input = window.prompt(mensaje);
    try {
      await this.state.contract.methods.CargaContrato().send({from: this.state.account, value: Web3.utils.toWei(String(Input),'ether')})
    }catch(err){
      this.setState({errorMessage: err.message})
      console.log("error:",err.message)
    } finally {
      this.setState({loading: false})
      this.loadBlockchainData()
    }
  }

  render() {
    return (
      <s.Screen image={FondoWeb}>
            <><s.SpacerSmall /><s.Container jc={"center"} fd={"row"} style={{ flexWrap: "wrap" }}>
              <s.Container flex={1} ai={"center"}>
                <s.TextDescription>{this.state.account}</s.TextDescription>
                <s.TextDescription2>{this.state.balance_account}</s.TextDescription2>
                <s.ButtonConectar style={{visibility: this.state.ButtonConectar}} onClick = {(e) => {e.preventDefault();this.ConectaMetamask();}}> 
                  <img src={MetamaskIco} alt="MetaMaskIco" style={{ resizeMode: "cover", height: 30 }}/>CONECTAR
                </s.ButtonConectar>
                <s.Container flex={1} ai={"center"} style={{visibility: this.state.AdminPanel}}>
                  <s.TextDescription>- AdminPanel -</s.TextDescription>
                  <s.ButtonAdmin onClick = {(e) => {e.preventDefault();this.AdminPrecioLlave("¿Precio Llave?");}}>PrecioLlave</s.ButtonAdmin>
                  <s.ButtonAdmin onClick = {(e) => {e.preventDefault();this.AdminPorcentPremio("¿Porcentaje Premio?");}}>%Premio</s.ButtonAdmin>
                  <s.ButtonAdmin onClick = {(e) => {e.preventDefault();this.AdminConfigLlaves("¿Config Llaves?\nDefault: [[50,10],[35,30],[15,50]]");}}>ConfigLlaves</s.ButtonAdmin>
                  <s.ButtonAdmin onClick = {(e) => {e.preventDefault();this.RegalaLlave("¿Address Regalo");}}>RegalaLlave</s.ButtonAdmin>
                  <s.ButtonAdmin onClick = {(e) => {e.preventDefault();this.AdminCargaCaja("¿Cuánto quieres Cargar?");}}>CargaCaja</s.ButtonAdmin>
                  <s.ButtonAdmin onClick = {(e) => {e.preventDefault();this.AdminRugPull("AdminRugPull");}}>RugPull</s.ButtonAdmin>
                  <s.TextDescription>Contract address:</s.TextDescription>
                  <s.TextDescription>{this.state.contract_address}</s.TextDescription>
                </s.Container>
              </s.Container>
              <s.Container flex={2} ai={"center"}>
                <s.TextTitle>- Abre la Caja -</s.TextTitle>
                <img src={SafeImage} alt="Safe" style={{ resizeMode: "cover", height: 250 }} />;
                <s.TextSubTitle>Contiene {this.state.Balance_Caja} BNB</s.TextSubTitle>
                <s.TextDescription>Si la abres ganarás <b>{this.state.Premio_Caja} BNB</b></s.TextDescription>
                <s.Button2 onClick = {(e) => {e.preventDefault();this.Mint_Llave("Minteando Llave",1);}}>
                  Compra 1 Llave por {this.state.Precio_Llave} BNB
                </s.Button2>
                <s.Button2 onClick = {(e) => {e.preventDefault();this.Mint_Llave("Minteando Llave",5);}}>
                  Compra 5 Llaves por {this.state.Precio_Llave * 5} BNB
                </s.Button2>
                <s.Button2 onClick = {(e) => {e.preventDefault();this.Mint_Llave("Minteando Llave",10);}}>
                  Compra 10 Llaves por {this.state.Precio_Llave * 10} BNB
                </s.Button2>
              </s.Container>
              <s.Container flex={1} ai={"center"} jc={"flex-end"} bc="lightblue">
                <s.TextTitle2>Como Jugar</s.TextTitle2>
                · Compra las llaves que quieras por {this.state.Precio_Llave} BNB<br></br>
                · Los BNB de compras de llaves van directos a la caja<br></br>
                · Hay 3 tipos de llave y este es su % de aparición:<br></br>
                <center>Iron: {this.state.MintIron}% - Silver: {this.state.MintSilver}% - Golden: {this.state.MintGold}%</center>
                <s.SpacerSmall />
                · Cada llave tiene un % de exito de abrir la caja:<br></br>
                <center>Iron: {this.state.JuegoIron}% - Silver: {this.state.JuegoSilver}% - Golden: {this.state.JuegoGold}%</center>
                <s.SpacerSmall />
                · Si abres la caja te llevas el {this.state.ProcentPremio}% de lo acumulado<br></br>
              </s.Container>
            </s.Container>
            <s.Container jc={"center"} fd={"row"} style={{ flexWrap: "wrap" }}>
              <s.Container flex={1} ai={"center"}>
                <s.TextTitle3>Tus Llaves ({this.state.MisLlaves.length}):</s.TextTitle3>
                <s.Container jc={"center"} fd={"row"} style={{ flexWrap: "wrap" }}>
                    {this.state.MisLlaves.map((item, index) =>
                      {
                        let Acierto = this.state.JuegoIron
                        // eslint-disable-next-line
                        if (item.rarity == 2){
                          Acierto = this.state.JuegoSilver
                          // eslint-disable-next-line
                        }else if (item.rarity == 3){
                          Acierto = this.state.JuegoGold
                        }
                        return ( 
                          <s.Container key = {index} style = {{padding: "15px"}} >
                            <s.TextDescription>#{item.id}</s.TextDescription>
                            <KeyRenderer llave = {item} />
                            <s.SpacerXSmall/>
                            <s.Container ai={"center"}>
                              <s.Button3 onClick = {(e) => {e.preventDefault();this.Usa_Llave(item.id,"Usa Llave");}}>
                                Usa
                              </s.Button3>
                              <s.TextDescription>{Acierto} %</s.TextDescription>
                              <s.SpacerXSmall/>
                            </s.Container>
                          </s.Container>
                        );
                      })
                    }
                </s.Container>
              </s.Container>
            </s.Container></>

      </s.Screen>
    );
  }
}
export default App;
