import React from "react";
import k_1 from "../assets/images/keys/IronKey.png"
import k_2 from "../assets/images/keys/SilverKey.png"
import k_3 from "../assets/images/keys/GoldKey.png"

export const keyparts = {
    keyimage: [k_1, k_2, k_3],
}

const KeyRenderer = ({llave = null, size = 150, style}) => {
    if(!llave) {
        return null;
    }

    let KeyImage = k_1;
    // eslint-disable-next-line
    if(llave.rarity == 2) {
        KeyImage = k_2;
    }
    // eslint-disable-next-line
    if(llave.rarity == 3) {
        KeyImage = k_3;
    }

    const keyStyle = {
        width: "100%",
        position: "absolute",
        border: "solid"
    }

    return (
        <div style = {{
            minWidth : size,
            minHeight : size,
            position: "relative",
            ...style,
        }}>
            <img alt ={KeyImage} src = {KeyImage}
            style = {keyStyle} />
        </div>
    );

};

export default KeyRenderer